import React from 'react';
import classnames from 'classnames';

const Section = ({ name = 'experience', children, hideTitle = false, className }) => {
    const titleClassNames = classnames('is-3', {'is-invisible': hideTitle})
    return (
        <section className={className ? `section ${className}` : 'section'}>
            <div className="content">
                <h3 className={titleClassNames}>{name}</h3>
                {children}
            </div>
        </section>
    )
};

export default Section;
